.info-list {
	font-size: 0;
	&__item {
		display: inline-block;
		vertical-align: top;
		// padding: 16px 0
		// +clr
		// +sm
 }		/// padding: 10px 0
	&__title {
		margin-bottom: 12px;
		color: $blue;
		@include f(20, 24, null, 700); }
	&__description {
		@include f(16, 24);
		@include sm {
			@include f(14, 22); } }
	&__title,
	&__description p {
		padding-top: 5px;
		padding-bottom: 5px;
		@include sm {
			padding-top: 2px;
			padding-bottom: 2px; } } }
