.info-block {
	padding: 213px 0 100px;
	color: $white;
	background: #011B37;
	@include lg {
		padding: 100px 0 60px; }
	@include sm {
		padding: 50px 0 28px; }
	&__header {
		margin: 0 auto 66px;
		text-align: left;
		@include lg {
			margin-bottom: 30px; }
		@include sm {
			margin-bottom: 40px; }
		&-in {
			overflow: hidden;
			padding: 4px 0 0;
			max-width: 770px; } }
	&__title {
		margin-bottom: 23px;
		text-align: left;
		@include lg {
			margin-bottom: 22px; }
		@include sm {
			margin-bottom: 17px; } }
	&__logo {
		float: left;
		margin: 0 38px 11px 0;
		width: 120px;
		@include sm {
			margin-bottom: 22px;
			width: 60px; } }
	&__content {
		margin: 0 auto 89px;
		@include lg {
			margin-bottom: 60px; }
		@include sm {
			margin-bottom: 30px; } }
	&__graph {
		padding: 20px 0;
		margin: 0 auto;
		max-width: 1000px;
		@include lg {
			padding: 40px 0;
			max-width: 597px; }
		@include md {
			padding: 18px 0; }
		@include sm {
			max-width: 100%; } } }
